<template>
    <div class="delayDetail" >
        <navigation-bar :pageName="title" @onLeftClick="goBack">
        </navigation-bar>
        <div class="box" v-if="delayDetail && workDetail">
            <div class="person-box list border">
                <div class="ele1">申请人</div>
                <div class="ele2"  v-if="delayDetail.userName">{{delayDetail.userName}}</div>
                <!--TODO <div>物业</div> -->
            </div>
            <!-- <div class="list border" v-if="workDetail.reporterPhone">
                <div class="ele1">报修人电话</div>
                <a class="ele2" :href="'tel:' + workDetail.reporterPhone">{{workDetail.reporterPhone}}</a>
            </div> -->
            <div class="person-box list border">
                <div class="ele1">申请时间</div>
                <div class="ele2">{{delayDetail.createdTime}}</div>
            </div>
            <div class="person-box list border">
                <div class="ele1">审批类型</div>
                <div class="ele2">{{delayDetail.approvalType == 0 ? "延迟处理时间" : "修改工单分类"}}</div>
            </div>
            <div>
              <div class="list border" v-if="delayDetail.approvalType == 0">
                  <div class="ele1">原处理时间</div>
                  <div class="ele2">{{delayDetail.receveLimitTime}}</div>
              </div>
              <div class="list border" v-if="delayDetail.approvalType == 0">
                <van-field
                    readonly
                    clickable
                    label="新处理时间"
                    :value="showTime ? showTime : ''"
                    input-align="right"
                    placeholder="请选择处理时间"
                    right-icon="arrow"
                    @click="showDatePicker = true"/>
              </div>
            </div>

            <div>
              <div class="list border" v-if="delayDetail.approvalType == 1">
                  <div class="ele1">原工单分类</div>
                  <div class="ele2">{{delayDetail.complCatCodeOldName}}</div>
              </div>
              <div class="list border" v-if="delayDetail.approvalType == 1">
                  <van-field
                    readonly
                    clickable
                    label="新工单分类"
                    :value="category? category.complCatName : ''"
                    input-align="right"
                    placeholder="请选择分类"
                    right-icon="arrow"
                    @click="selecCategory"/>
              </div>
            </div>
            <!-- <div class="list">
                <div class="ele1">延期时长</div>
                <div class="ele2">{{delayDetail.TIME_LIMIT}}</div>
            </div> -->

            <div class="list border apply">
                <div class="ele1">申请内容</div>
                <div class="ele2 apply-content">{{delayDetail.reason}}</div>
            </div>

            <div class="list">
              <div class="ele1">审批意见</div>
            </div>
            <div class="list">
              <textarea name="" placeholder="请输入审批意见..." v-model="checkContent" maxlength="100" @input="filterContent"></textarea>
            </div>

            <div class="number-box list border">
                <div class="number ele2">工单编号： {{complCode}}</div>
                <div class="fontGreen status" v-if="workDetail.status == '1'">受理中</div>
                <div class="fontYellow status" v-if="workDetail.status == '2' && (workDetail.closeType != '1' && workDetail.closeType != '2')">待评价</div>
                <div class="fontBlack status" v-if="workDetail.status == '2' && workDetail.closeType == '1'">暂时关闭</div>
                <div class="fontBlack status" v-if="workDetail.status == '2' && workDetail.closeType == '2'">非正常关闭</div>
                <div class="fontBlack status" v-if="workDetail.status == '3' && workDetail.closeType != '2'">已结束</div>
                <div class="fontBlack status" v-if="workDetail.status == '3' && workDetail.closeType == '2'">非正常关闭</div>
            </div>

            <div class="building-box list border">
                <div class="building ele1 overflow-es">{{workDetail.areaName}} {{workDetail.buildName}} {{workDetail.unitNo ? workDetail.unitNo + "- " : ''}}{{workDetail.roomName}}</div>
                <div class="time ele2">{{workDetail.createdTime}}</div>
            </div>
            <div class="repair-box list">
                <div class="type" v-if="workDetail.typeFlag == 1">投诉</div>
                <div class="type" v-if="workDetail.typeFlag == 2 || workDetail.typeFlag == 0">报修</div>
                <div class="dis ele1">{{workDetail.content}}</div>
            </div>

            <div class="img-box list" v-if="workDetail.picUrlList && workDetail.picUrlList.length > 0">
              <img :src="item" alt="" v-for="(item, index) in workDetail.picUrlList" :key="index" @click="preview(workDetail.picUrlList, index)">
            </div>

            <div class="score-box" v-if="workDetail.evalInfo">
              <div class="score-top">
                <div class="score">
                  <img :src="starIcon" alt="" v-for="(item) in workDetail.evalInfo.evalLevel" :key="'item' + item">
                  <img :src="starBackIcon" alt="" v-for="(item, index) in (5 - workDetail.evalInfo.evalLevel)" :key="'index' + index">
                </div>
                <div class="time">{{workDetail.evalInfo.evalTime}}</div>
              </div>
              <div class="dis">{{workDetail.evalInfo.evalContg}}</div>
            </div>

            <div class="time-box list border" v-if="workDetail.typeFlag == 0 || workDetail.typeFlag == 1 || workDetail.typeFlag == 2 || workDetail.typeFlag == 4">
                <div class="ele1" v-if="workDetail.typeFlag == 0">确认时限</div>
                <div class="ele1" v-else>处理时限</div>


                <div class="ele2 time" v-if="workDetail.typeFlag == 0">{{workDetail.receiver.confLimitTime}}</div>
                <div class="ele2 time" v-else>{{workDetail.receiver.compLimitTime}}</div>
            </div>
            <div class="person-box list border" v-if="workDetail.ownerName">
                <div class="ele1">录入人</div>
                <div class="ele2">{{workDetail.ownerName}}</div>
            </div>
            <div class="tel-box list" v-if="workDetail.ownerPhone">
                <div class="ele1">录入人电话</div>
                <a class="ele2" :href="'tel:' + workDetail.ownerPhone">{{workDetail.ownerPhone}}</a>
            </div>

            <div class="process-box">
                <div class="title ele2">受理过程</div>
                <div class="process">
                    <div class="item" v-for="(item,index) in workDetail.detailList" :key="index">
                        <div class="status-box" :class="{ 'status1': index == 0, 'status2': index != 0 }">
                            <div class="status-left">
                              <div class="nodeNow" v-if="index == 0">
                                <img :src="nodeNowIcon" alt="">
                              </div>
                              <div class="node" v-if="index != 0">
                                <img :src="nodeIcon" alt="">
                              </div>
                              <div v-if="item.operaTypeFlag == 0">受理</div>
                              <div v-if="item.operaTypeFlag == 1">指派</div>
                              <div class="assign" v-if="item.operaTypeFlag == 1">
                                <img :src="pointIcon" alt="">
                              </div>
                              <div v-if="item.operaTypeFlag == 1 && item.nextPostName">({{item.nextPostName}})</div>
                              <div v-if="item.operaTypeFlag == 1">{{item.nextUserName}}</div>
                              <div v-if="item.operaTypeFlag == 2">接单</div>
                              <div v-if="item.operaTypeFlag == 3">评价</div>
                              <div v-if="item.operaTypeFlag == 4">问卷</div>
                              <div v-if="item.operaTypeFlag == 5">完成</div>
                              <div v-if="item.operaTypeFlag == 6">受理通知</div>
                              <div v-if="item.operaTypeFlag == 7">延期申请</div>
                              <div v-if="item.operaTypeFlag == 8">延时审批</div>
                              <div v-if="item.operaTypeFlag == 9">录入情况说明</div>
                              <div v-if="item.operaTypeFlag == 10">发起</div>
                              <div v-if="item.operaTypeFlag == 11">关闭</div>
                              <div v-if="item.operaTypeFlag == 12">修改工单类型</div>
                              <div v-if="item.operaTypeFlag == 13">变更回访人</div>
                              <div v-if="item.operaTypeFlag == 14">非正常关闭</div>
                              <div v-if="item.operaTypeFlag == 15">关联工单</div>
                              <div v-if="item.operaTypeFlag == 16">审批修改工单分类</div>
                              <div v-if="item.operaTypeFlag == 17">合并时限</div>
                              <div v-if="item.operaTypeFlag == 18">修改审批人</div>
                              <div v-if="item.operaTypeFlag == 19">催单</div>
                            </div>
                            <div class="time">{{item.createdTime}}</div>
                        </div>
                        <div class="content-box" :class="{ 'status1': index == 0, 'status2': index != 0 }">
                          <div class="content">
                            <div class="name" v-if="item.operaTypeFlag != 6">
                              <!-- 其他 -->
                              <div class="left" v-if="item.operaTypeFlag != 10 && item.operaTypeFlag != 3">
                                <div v-if="item.postName">{{item.postName}}_</div>
                                <div>{{item.userName}}</div>
                              </div>
                              <!-- 评价 -->
                              <div class="left" v-if="item.operaTypeFlag == 3">
                                <div v-if="item.postName && workDetail.evalInfo.origFlag=='1'">{{item.postName}}_</div>
                                <div>{{item.userName}}</div>
                              </div>
                              <!-- 发起 -->
                              <div class="left" v-if="item.operaTypeFlag == 10">
                                <div v-if="workDetail.postName">{{workDetail.postName}}_</div>
                                <div>{{workDetail.ownerFlag ==0 ?workDetail.ownerName :workDetail.userName}}</div>
                              </div>
                              <a :href="'tel:' + workDetail.ownerFlag == 0 ? workDetail.ownerPhone :workDetail.userPhone" class="fLeft" v-if="item.operaTypeFlag == 10">{{workDetail.ownerFlag ==0 ? workDetail.ownerPhone :workDetail.userPhone}}</a>
                              <a :href="'tel:' + item.phone" class="fLeft" v-if="item.operaTypeFlag != 10">{{item.phone}}</a>
                            </div>
                            <div class="dis">{{item.content}}</div>
                            <!-- 合并时限 -->
                            <div class="dis" v-if="item.operaTypeFlag == 17">合并后处理时限为：{{item.receveTime}}</div>
                            <div class="detail-img" v-if="item.picUrlList && item.picUrlList.length > 0">
                              <img :src="image" alt="" v-for="(image, index2) in item.picUrlList" :key="index2" @click="preview(item.picUrlList, index2)">
                            </div>
                          </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="btn-box">
            <div @click="check(1)">同意</div>
            <div @click="check(2)" class="disagree">驳回</div>
        </div>

        <van-popup v-model="showDatePicker" position="bottom">
          <van-datetime-picker
            v-model="workDate"
            type="datetime"
            title="选择延时时间"
            :min-date="minDate"
            :max-date="maxDate"
            @cancel="showDatePicker = false"
            @confirm="selectDate"
            />
        </van-popup>

        <van-popup v-model="showCategoryModel" position="bottom">
          <div class="popup-top">
            <div class="cancel" @click="hideModel">
              <img :src="leftIcon" alt="">
            </div>
            <div class="catTitle">{{catTitle}}</div>
            <!-- <div class="back" @click="backToLastLevel" v-if="uuidParents.length > 1">
              返回上一级
            </div> -->
          </div>
          <div class="dis-tab">
            <div class="content-div" v-for="(item, index) in tabs" @click="toClass(item)" >
              <div class="dis-tab-div" v-if="classData.level != index">
                <div class="type-tab">{{item.catName}}</div>
              </div>
              <div class="dis-tab-div-sel" v-if="classData.level == index">
                <div class="type-tab" >{{item.catName}}</div>
              </div>
            </div>
          </div>
          <div class="popup-bottom">
            <div v-for="(item, index) in classList" :key="index" @click="nextClass(item)">
              <div class="detail-cell section-select" v-if="item.uuid != selectTab.uuid">
                  <div class="left-nosel" >{{item.complCatName}}</div>
              </div>
              <div class="detail-cell section-select" v-if="item.uuid == selectTab.uuid">
                  <div class="left" >{{item.complCatName}}</div>
                  <div class="right">
                      <van-icon name="success" size="15px" class="succ-right"/>
                  </div>
              </div>
            </div>
          </div>
        </van-popup>
    </div>
</template>

<script>
import {
  getInterDelayDetail,
  checkWorkDelay
} from "../../services/internalWorkDelayService";
import {
  getClassList,
  getWorkPlanDetail
} from "../../services/workPlanService";
import { store } from "../../utils/store";
import log from "../../utils/log";
import leftIcon from "@/assets/imgs/workPlan/left_black.png";
import starIcon from "@/assets/imgs/workPlan/star.png";
import starBackIcon from "@/assets/imgs/workPlan/star_back.png";
import nodeNowIcon from "@/assets/imgs/workPlan/nodeNow.png";
import nodeIcon from "@/assets/imgs/workPlan/node.png";
import pointIcon from "@/assets/imgs/workPlan/point.png";
import classIcon from "@/assets/imgs/workDelay/class.png";
import { ImagePreview } from "vant";
import moment from 'moment';
import NavigationBar from "../title/NavigationBar";
export default {
  name: "delayDetailInside",
  components: { NavigationBar },
  data() {
    return {
      delayDetail: null,
      workDetail: null,
      title: "",
      userInfo: null,
      checkContent: "",
      delayUuid: "",
      complCode: "",
      showDatePicker: false,
      showTime: "",
      workDate: new Date(),
      minDate: new Date(1919, 0, 1),
      maxDate: new Date(2090, 11, 31),

      category: "",
      showCategoryModel: false,
      applyType: null,
      // 获取工单分类目录需要的参数值
      uuidParents: [],
      classData: {
        uuidParent: "0",
        complResource: 1,
        level: 0,
        deptCode: ''
      },
      classList: [],

      leftIcon: leftIcon,
      starIcon: starIcon,
      starBackIcon: starBackIcon,
      nodeNowIcon: nodeNowIcon,
      nodeIcon: nodeIcon,
      pointIcon: pointIcon,
      classIcon: classIcon,

      isFromHome: false,
      tabs: [{uuid: 0, catName:"请选择"}],
      catTitle: "请选择一级分类",
      selectTab: {uuid: 0, catName:"请选择"},
      imagePreview: '',
    };
  },
  created() {
    this.userInfo = store.get("userInfo");
    this.complCode = this.$route.params.complCode;
    this.delayUuid = this.$route.params.uuid;
    this.delayDetail = null;
    this.getDelayDetail();
    this.getWorkPlanDetail();
  },
  updated() {
    if (this.showCategoryModel) {
      if (this.classData.level == this.tabs.length - 1) {
        // scroll to the cat tab end when receive model show
        document.querySelector(".dis-tab").scrollLeft = 999999;
      } else if (this.classData.level == 0) {
        document.querySelector(".dis-tab").scrollLeft = 0;
      } else {
        let disTab = document.querySelector('.dis-tab');
        let tabSelect = document.querySelector('.dis-tab-div-sel');
        let leftOffset = tabSelect.offsetLeft - disTab.scrollLeft;
        let widthOffset = disTab.offsetWidth - tabSelect.offsetLeft;
        log.l("leftOffset value " + leftOffset + " - widthOffset value " + widthOffset + " - selectWidth value " + tabSelect.offsetWidth);
        if (leftOffset < 40) {
          disTab.scrollLeft = disTab.scrollLeft - 2 * tabSelect.offsetWidth;
        } else if (widthOffset < 40) {
          disTab.scrollLeft = disTab.scrollLeft + 2 * tabSelect.offsetWidth;
        }
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    if(from.name == "home") {
      next(vm => {
        vm.isFromHome = true;
      });
    } else {
      next();
    }
  },
  beforeRouteLeave (to, from, next) {
    if(this.imagePreview) {
      this.imagePreview.close();
    }
    next();
  },
  methods: {
    /**
     * 获取延期审批详情
     */
    getDelayDetail: function() {
      console.log(this.userInfo);
      var data = {
          uuid: this.delayUuid
      };
      getInterDelayDetail(data).then(
        res => {
          if (res.resultCode == 0) {
            this.setDelayDetail(res.data);
          } else {
            this.$toast(res.msg);
            this.$router.go(-1);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 设置延期审批详情
     */
    setDelayDetail(res) {
      this.delayDetail = res;
      this.title = this.delayDetail.approvalType == 0 ? "延时审批（内部）" : "修改分类审批（内部）";
      if (this.delayDetail.approvalType == 0) {
        this.showTime = this.delayDetail.receveDelayTime;
        this.workDate = new Date(this.delayDetail.receveDelayTime);
      } else {
        this.category = {
          complCatCode: this.delayDetail.complCatCodeNew,
          complCatName: this.delayDetail.complCatCodeNewName
        };
      }
    },

    /**
     * 获取工单详情
     */
    getWorkPlanDetail: function() {
      var data = {
        complCode: this.complCode,
        userCode: this.userInfo.USER_CODE
      };
      getWorkPlanDetail(data).then(
        res => {
          if(res.resultCode == 0) {
            this.workDetail = res.data;
          } else {
            this.$toast(res.msg)
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 预览图片
     */
    preview: function(arr, index) {
      this.imagePreview = ImagePreview({
        images: arr,
        startPosition: index,
        onClose() {}
      });
    },

    /**
     * 禁止输入表情
     */
    filterContent: function() {
      this.checkContent = this.checkContent.replace(
        /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/gi,
        ""
      );
    },

    /**
     * 选择日期事件
     */
    selectDate: function(value) {
        this.showDatePicker = false;
        if (this.delayDetail &&
            this.delayDetail.receveLimitTime &&
            value) {
            let showTimeDate = this.formatter(value);
            let limitTimeDate = this.delayDetail.receveLimitTime;
            if (moment(showTimeDate) < moment(limitTimeDate)) {
                this.$toast("延期时间必须大于规定时限，请重新选择延期时间！");
                return;
            }
        }
        this.showTime = this.formatter(value);
        this.workDate = value;
    },

    /**
     * 格式化日期
     */
    formatter: function(date) {
      return (
        date.getFullYear() +
        "-" +
        this.getTime(date.getMonth() + 1) +
        "-" +
        this.getTime(date.getDate()) +
        " " +
        this.getTime(date.getHours()) +
        ":" +
        this.getTime(date.getMinutes()) +
        ":00"
      );
    },

    /**
     * 获取规则的格式
     */
    getTime(time) {
      if (time < 10) {
        return "0" + time;
      } else {
        return time;
      }
    },

    /**
     * 返回上一级
     */
    backToLastLevel: function() {
      this.uuidParents.pop();
      this.classData.uuidParent = this.uuidParents[
        this.uuidParents.length - 1
      ];
      this.tabs = [{uuid: 0, catName:"请选择"}];
      this.classData.level = 0;
      this.catTitle = "请选择一级分类";
      this.selecCategory();
    },

    /**
     * 点击列表进入下一级
     */
    nextClass: function(item) {
      if (item.isLeafFlag == 0) {
        // 不是最后一级
        this.classData.uuidParent = item.uuid;
        if (this.classData.level < this.tabs.length - 1) {
          // update tabs
          let levelIndex = this.classData.level;
          this.tabs = this.tabs.slice(0, levelIndex);
        } else {
          this.tabs.pop();
        }
        this.tabs.push({uuid:item.uuid, catName:this.getCatName(item.complCatName)});
        this.selectTab = {uuid: 0, catName:"请选择"};
        this.tabs.push(this.selectTab);
        this.classData.level++;
        this.selecCategory();
      } else {
        this.category = item;
        this.showCategoryModel = false;
        this.tabs = [{uuid: 0, catName:"请选择"}];
        this.selectTab = {uuid: 0, catName:"请选择"};
        this.classList = [];
        this.classData = {
          uuidParent: "0",
          complResource: 1,
          level: 0,
          deptCode: this.userInfo.deptCode,
        };
      }
    },

    /**
    * 进入指定分类级别
    **/
    toClass: function(item) {
      if (item.uuid == this.selectTab.uuid) {
        log.i("DelayDetailInSide: select the same tab, ignore.");
        return;
      }
      // update class list
      let levelIndex = this.tabs.indexOf(item);
      let lastItem = this.tabs[levelIndex - 1];
      this.selectTab = item;
      if (lastItem) {
        this.classData.uuidParent = lastItem.uuid;
        this.classData.level = levelIndex;
        this.selecCategory();
        // update tab list
        /*this.tabs = this.tabs.slice(0, levelIndex);
        this.tabs.push({uuid: 0, catName:"请选择"});*/
      } else {
        this.classData = {
          uuidParent: "0",
          complResource: 1,
          level: 0,
          deptCode: this.userInfo.deptCode
        };
        // this.tabs = [{uuid: 0, catName:"请选择"}];
        this.selecCategory();
      }
    },

    getCatName: function(complCatName) {
      if (!complCatName) {
        return null;
      }
      if (complCatName.length <= 5) {
        return complCatName;
      } else {
        return complCatName.slice(0, 2) + "..." + complCatName.slice(complCatName.length - 2);
      }
    },

    /**
     * 选择工单分类点击事件
     */
    selecCategory: function() {
      if (
        this.classData.uuidParent !=
        this.uuidParents[this.uuidParents.length - 1]
      ) {
        this.uuidParents.push(this.classData.uuidParent);
      }
      this.classData.complResource = 1;
      this.classData.deptCode = this.userInfo.deptCode;
      getClassList(this.classData).then(
        res => {
          this.classList = res.data;
          this.showCategoryModel = true;
          switch (this.classData.level + 1) {
            case 1:
              this.catTitle = "选择一级分类";
              break;
            case 2:
              this.catTitle = "选择二级分类";
              break;
            case 3:
              this.catTitle = "选择三级分类";
              break;
            case 4:
              this.catTitle = "选择四级分类";
              break;
            case 5:
              this.catTitle = "选择五级分类";
              break;
            case 6:
              this.catTitle = "选择六级分类";
              break;
            case 7:
              this.catTitle = "选择七级分类";
              break;
            case 8:
              this.catTitle = "选择八级分类";
              break;
            case 9:
              this.catTitle = "选择九级分类";
              break;
            case 10:
              this.catTitle = "选择十级分类";
              break;
            default:
              // not care
              this.catTitle = "选择超十级分类";
              break
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 取消弹窗
     */
    hideModel: function() {
      this.classData.uuidParent = "0";
      this.uuidParents = [];
      this.showCategoryModel = false;
      this.tabs = [{uuid: 0, catName:"请选择"}];
      this.classData.level = 0;
      this.catTitle = "请选择一级分类";
      this.classList = [];
      this.selectTab = {uuid: 0, catName:"请选择"};
    },

    /**
     * 进行审核
     */
    check: function(status) {
      if (status == 1
          && this.delayDetail.approvalType == 0
          && this.delayDetail
          && this.delayDetail.receveLimitTime
          && this.showTime) {
          let showTimeDate = this.showTime;
          let limitTimeDate = this.delayDetail.receveLimitTime;
          if (moment(showTimeDate) < moment(limitTimeDate)) {
              this.$toast("延期时间必须大于规定时限！");
              return;
          }
      }
      var checkData = {
        approvalType: this.delayDetail.approvalType,
        uuid: this.delayDetail.uuid,
        userCode: this.userInfo.USER_CODE,
        complCode: this.complCode, //工单号,
        result: status,
        approvalDelayTime: this.showTime || '',
        complCatCode: this.category.complCatCode || '',
        approvalRemark: this.checkContent || "无",
        areaCode: this.workDetail.areaCode
      };
      checkWorkDelay(checkData).then(
        res => {
          if(res.resultCode == 0) {
            this.$toast("审核成功");
            store.set("workDelayListStatus", 2);
            this.$router.go(-1);
          } else {
            this.$toast(res.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 返回事件
     */
    goBack: function() {
      if(this.isFromHome) {
        this.isFromHome = false;
        store.dele("workDelayListStatus");
      } else {
        store.set("workDelayListStatus", 0);
      }
      this.$router.go(-1);
    }
  },
  computed: {}
};
</script>
